@import "./reset.scss";
@import "./variables/global.scss";

html {
  font-size: 16px;
  .app {
    // font: var(--font-m);
    font-size: 16px;
    color: var(--color-text-light);
    width: 100vw;
    min-height: 100vh;
    background-color: var(--color-bg-dark3);

    .content-page {
      background-color: var(--color-bg-dark3);
      padding-top: var(--navbar-height);
      width: 100%;
      min-height: 100%;
      margin: auto;

      .page-wrapper {
        width: 90%;
        min-height: calc(100vh - var(--navbar-height));
        background-color: var(--color-bg-dark3);
      }
    }
  }
}
